import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import artifactsData from './artifactsData'; // Import the artifact data

// Styled components
const PageContainer = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
`;

const HeaderLeft = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 5%;
`;

const Icon = styled.img`
  margin-right: 10px;
`;

const Title = styled.h1`
  font-size: 18px;
  margin: 0;
`;

const SubTitle = styled.p`
  margin: 0;
  font-size: 14px;
  color: #666;
`;

const SearchContainer = styled.div`
  float: right;
  align-items: center;
  margin-bottom: 5%;
`;

const SearchInput = styled.input`
  padding: 10px;
  font-size: 14px;
  width: 20%;
  background-color: #D8D8D8;
  border: none;
  border-radius: 10px;
`;

const FilterContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
`;

const FilterRow = styled.div`
  display: flex;
  gap: 10px;
  align-items: center;
`;

const FilterTitle = styled.p`
  font-size: 14px;
`;

const FilterOption = styled.img`
  cursor: pointer;
  border: ${({ selected }) => (selected ? '2.5px solid #000' : '2px solid transparent')};
  border-radius: 10px;
`;

const ArtifactsGrid = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 1200px;
`;

const ArtifactCard = styled.div`
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  padding: 10px;
  width: 90%; /* Fixed width */
  height: 90%; /* Fixed height */
`;

const ArtifactImage = styled.img`
  width: 80px; /* Fixed width */
  height: 80px; /* Fixed height */
  border-radius: 10px;
`;

const ArtifactDetails = styled.div`
  display: flex;
  flex-direction: column;
  width: calc(100% - 100px); /* Adjust to fit the card with padding and image */
  margin-left: 3%;
`;

const ArtifactName = styled.p`
  color: black;
  margin: 0px;
  padding: 5px;
  border-radius: 0 0 10px 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const IconContainer = styled.div`
  display: flex;
  align-items: center;
  margin-left: 5%;
`;

const gradeOptions = ['common', 'rare', 'epic'];
const typeOptions = ['shooting', 'assist', 'cut', 'charge'];

const ArtifactsInfo = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedGrades, setSelectedGrades] = useState([]);
  const [selectedTypes, setSelectedTypes] = useState([]);
  const navigate = useNavigate();

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const handleFilterClick = (filterType, value) => {
    const updateFilter = (filter, setFilter) => {
      if (filter.includes(value)) {
        setFilter(filter.filter((item) => item !== value));
      } else {
        setFilter([...filter, value]);
      }
    };

    if (filterType === 'grade') {
      updateFilter(selectedGrades, setSelectedGrades);
    } else if (filterType === 'type') {
      updateFilter(selectedTypes, setSelectedTypes);
    }
  };

  const handleArtifactClick = (artifactPath) => {
    navigate(`/artifacts/${artifactPath}`);
  };

  const filteredArtifacts = artifactsData.filter((artifact) => {
    return (
      (selectedGrades.length === 0 || selectedGrades.includes(artifact.grade)) &&
      (selectedTypes.length === 0 || selectedTypes.includes(artifact.type)) &&
      artifact.name.toLowerCase().includes(searchTerm.toLowerCase())
    );
  });

  return (
    <PageContainer>
      <HeaderLeft>
        <Icon src="https://sseong-mi.github.io/Tower-of-Adventures/images/items/cocodrop.png" alt="Icon" />
        <div>
          <Title>아티팩트 정보</Title>
          <SubTitle>세부 정보를 알고싶다면 아티팩트를 클릭하세요!</SubTitle>
        </div>
      </HeaderLeft>
      <SearchContainer>
        <SearchInput
          type="text"
          placeholder="검색"
          value={searchTerm}
          onChange={handleSearchChange}
        />
      </SearchContainer>
      <FilterContainer>
        <FilterRow>
          <FilterTitle>등급</FilterTitle>
          {gradeOptions.map((grade) => (
            <FilterOption
              key={grade}
              src={`https://sseong-mi.github.io/Tower-of-Adventures/images/grades/${grade}.png`}
              alt={grade}
              selected={selectedGrades.includes(grade)}
              onClick={() => handleFilterClick('grade', grade)}
            />
          ))}
        </FilterRow>
        <FilterRow>
          <FilterTitle>타입</FilterTitle>
          {typeOptions.map((type) => (
            <FilterOption
              key={type}
              src={`https://sseong-mi.github.io/Tower-of-Adventures/images/types/${type}.png`}
              alt={type}
              selected={selectedTypes.includes(type)}
              onClick={() => handleFilterClick('type', type)}
            />
          ))}
        </FilterRow>
      </FilterContainer>
      <ArtifactsGrid>
        {filteredArtifacts.map((artifact) => (
          <ArtifactCard key={artifact.name} onClick={() => handleArtifactClick(artifact.path)}>
              <ArtifactImage src={artifact.image} alt={artifact.name} />
            {artifact.type !== 'none' && (
              <IconContainer>
                <img src={`https://sseong-mi.github.io/Tower-of-Adventures/images/types/${artifact.type}.png`} alt={artifact.type} width="20" />
              </IconContainer>
            )}
            <ArtifactDetails>
              <ArtifactName>{artifact.name}</ArtifactName>
            </ArtifactDetails>
          </ArtifactCard>
        ))}
      </ArtifactsGrid>
    </PageContainer>
  );
};

export default ArtifactsInfo;
